$main-red: #bf0135;
$main-black: black;
$main-white: white;
$roundness: 4px;
$card-padding: 15px;

@mixin font-regular {
    font-family: 'Manrope', sans-serif !important;
    font-weight: 400;
}

@mixin font-semibold {
    font-family: 'Manrope', sans-serif !important;
    font-weight: 600;
}

@mixin font-bold {
    font-family: 'Manrope', sans-serif !important;
    font-weight: 700;
}