@import '../../../vars';

input{
    outline: none;
    border:none;
    border-radius: 0;
    border-bottom: 1px solid gray;
    font-size: 16px !important;
    width: 100%;
}

.keyholder{
    position: absolute;  
}

.tiles-keyholder {
    left: -5vw;
}

.tiles-container {
    top:0px;
    width: 90vw;
    display: flex;
    margin: auto;
    flex-direction: column;
    justify-content: flex-start;
    background:$main-white;
    color: $main-black;
    z-index: 100;

    .method-tile {
        overflow: hidden;
        height: 180px;
        width: 100%;
        border-radius: $roundness;
        margin: 5px 0;
        padding: 20px;
        color: $main-white;
        cursor: pointer;

        .content {
            height: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            .text {
                text-align: right;
                font-weight: 600;
                width: 200px;
            }

            .left {
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .icon {
                    font-size: 3em;
                    border-bottom: solid 4px white;

                    &.percent {
                       transform: scaleY(.85) scale(.9);
                       padding-bottom: 45px;
                       margin-left: -10px;
                    }
                }
            }
        }
    }

    .tiles{
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-start;
        // height: 60%;
        width: 100%;

        .credit {
            background: url('../../../assets/first.png') no-repeat;
            background-size: cover; 
        }
    
        .vklad {
            background: url('../../../assets/second.png') no-repeat;
            background-size: cover;
        }
    
        .nakop {
            background: url('../../../assets/therd.png') no-repeat;
            background-size: cover;
        }
    
        .credit_title{
            font-weight: 200;
            font-size: 1.7em;
            color: $main-white;
        }
        .nakop_title{
            font-size: 1.7em;
            color: $main-white;
        }
        .vklad_title{
            font-size: 1.7em;
            color: $main-white;
        }
    }
}

.form{
    position: relative;
    left: 5vw;
    top:0px;
    width: 90vw;
    height: 75vh;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    justify-content: flex-end;
    background: $main-white;
    color: $main-black;
    z-index: 100;
    
    .new-goal-button {
        &.acc {
            margin: 0 !important;
        }
    }

    .field {

        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin: 15px auto;

        .goal_date{
            font-size: 16px;
            margin: 0;
        }
    }

    .back {
        position: relative;
        top: 10px;
        left: -10vw;
    }

    .title{
        font-size: 1.4em;
    }

    .label{
        font-weight: bold;
        margin-bottom:0;
    }
    .personal{
        font-weight: bold;
        margin-bottom:0;
        height: 40px;
        width:100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        word-wrap: none;

        input {
            width: 150px;
        }
    }

    .inputs_holder{
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-start;
        // height: 60%;
        width: 100%;

        .installment {
            background: url('../../../assets/first.png') no-repeat;
            background-size: cover; 
        }

        .method-tile {
            overflow: hidden;
            height: 180px;
            width: 100%;
            border-radius: $roundness;
            margin: 5px 0;
            padding: 20px;
            color: $main-white;
            cursor: pointer;

            .credit_title{
                font-weight: 200;
                font-size: 1.7em;
                color: $main-white;
            }
    
            .content {
                height: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
    
                .text {
                    text-align: right;
                    font-weight: 600;
                    width: 200px;
                }
    
                .left {
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
    
                    .icon {
                        font-size: 3em;
                        border-bottom: solid 4px $main-white;
    
                        &.percent {
                           transform: scaleY(.85) scale(.9);
                           padding-bottom: 45px;
                           margin-left: -10px;
                        }
                    }
                }
            }
        }

        .rocket {
            width: 80%;
            margin: 0 auto;
        }

        .target {
            width: 80%;
            margin: 0 auto;
        }
    }

    .choose{
        width: 85%;
        margin-top: 10%;
    }

    
    .cost{
        @include font-regular;
    }
    .cost_month{
        @include font-regular;
        font-size: 16px;
    }

    .submit{
        color: $main-red;
        width: 30%;
        height: 40px;
        background:white;
        border: 1px solid $main-red;
        border-radius: $roundness;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;

        &:hover{
            color: $main-white;
            background: $main-red;
        }
    }


    .close{
        transition: all 0.6s ease;
        position: absolute;
        z-index: 150;
        top:10px;
        left: 10px;
        height: 25px;
        width: auto;

        img{
            height: 100%;
            width: auto;
        }
    }


    .progress{
        height: 20px;
        width: 100%;
        background: #d8d8d8;
        border-radius: $roundness;
        overflow: hidden;

        div{
            height: 20px;
            background:black;
        }
    }

    .indicator{
        color: $main-black;
        font-weight: 600;

    }

}


.example-enter {
    opacity: 0;
    transform: translateX(-100%);
    transition: all .5s ease-in;
  }
  
  .example-enter.example-enter-active {
    opacity: 1;
    transform: translateX(0px)
  }
  
  .example-leave {
    transition: all .5s ease-in;
    transform: translateX(0px);
    opacity: 1;
  }
  
  .example-leave.example-leave-active {
    opacity: 0;
    transform: translateX(100%)
  }